import React from "react";
import "./Profile.css";

import user_img from "../../../assets/media/user.png";
import dropdown_icon from "../../../assets/media/bi_chevron-down.svg";

const Profile = () => {
    return (
        <div className="profile">
            <img src={user_img} alt="user image" className="profile--image" />
            <div className="profile--details">
                <h4 className="username">Joseph Ehikioya</h4>
                <p className="role">Super Admin</p>
            </div>
            <img src={dropdown_icon} alt="dropdown icon" className="dropdown" />
        </div>
    );
}

export default Profile;
