import React from "react";
import "./Search.css";

import dropdown_icon from "../../assets/media/bi_chevron-down-1.svg";

const Search = () => {
    return (
        <div className="search">
            <input type="text" className="search--field" placeholder="Search for a target" />
            <button className="search--btn">Search</button>
            <button className="search--filter">
                <span>Filter</span>
                <img src={dropdown_icon} alt="dropdown" className="dropdown--icon"/>
            </button>
            <div className="clear">Clear Filter</div>
        </div>
    )
}

export default Search;