import React, { useState } from "react"
import "./Boards.css";

import Board from "../Boards/Board/Board";

const Boards = () => {
    const [targets, setTargets] = useState([
        {
            title: "All Targets",
            numOfBranches: 52,
            total_targets: 60,
            achieved_targets: 30,
            targets_left: 30
        },
        {
            title: "Product Target",
            total: 25,
            achieved_targets: 15,
            targets_left: 10
        },
        {
            title: "Sales Target",
            total: 35,
            achieved_targets: 15,
            targets_left: 20
        }
    ]);
    return (
        <div className="boards">
            {targets.map(target => <Board key={target.title} target={target} />)}
        </div>
    )
}

export default Boards;