import React from "react";
import { Link } from "react-router-dom";

import "./Tabs.css";

const Tabs = () => {
    return (
        <ul className="tab">
            <li className="tab--item">
                <Link className="tab--link" to="/targets">Targets</Link>
            </li>
            <li className="tab--item">
                <Link className="tab--link active" to="/achievements">Achievements</Link>
            </li>
        </ul>
    )
}

export default Tabs;