import React from "react";
import "./Board.css";

import ProgressBar from "../../ProgressBar/ProgressBar";

const Board = (props) => {
    const { numOfBranches, title, total_targets, achieved_targets, targets_left, total } = props.target;
    const changeTitle = (title) => {
        if (title === "All Targets") {
            return "board--blue"
        } else if (title === "Product Target") {
            return "board--yellow"
        } else {
            return "board--purple"
        }
    }
    return (
        <div className={`board ${changeTitle(title)}`}>
            <div className="board--header">
                <h3 className="board--title">{title}</h3>
                {numOfBranches ? 
                <div className="branch">
                    <p>Number of Branches</p>
                    <span className="branch--numbers">{ numOfBranches }</span>
                </div> : null
            }
                
            </div>
            <div className="board--stats">
                <div className="stat">
                    <p className="stat--title">{ total_targets ? 'Total Targets' : 'Total' }</p>
                    <span className="stat--count">{ total_targets ? total_targets : total }</span>
                </div>
                <div className="stat">
                    <p className="stat--title">Achieved Targets</p>
                    <span className="stat--count">{ achieved_targets }</span>
                </div>
                <div className="stat">
                    <p className="stat--title">Targets Left</p>
                    <span className="stat--count">{ targets_left }</span>
                </div>
            </div>
            <ProgressBar />
        </div>
    )
}

export default Board;