import React from "react";
import { connect } from "react-redux";
import "./SearchResults.css";

import TargetCard from "../TargetCard/TargetCard";

const SearchResults = (props) => {
    const { targets } = props.targets;
    return (
        <div className="search--results">
            <h4 className="search--results-heading">Monitor your targets and achievements below</h4>
            <div className="results">
                {targets ? targets.map(target => <TargetCard key={target.title} target={target} />) : null}
            </div>
        </div>
    )
}

const mapStateToProps = (state) => {
    return { targets: state.targets };
}

export default connect(mapStateToProps)(SearchResults);