import React from "react"
import "./TargetCard.css";

import location_icon from "../../assets/media/location.svg";

import ProgressBar from "../ProgressBar/ProgressBar";

const TargetCard = ({ target }) => {
    const { title, location, month_target, achived, left_to_go, daily_target, percentage_complete, percentage_left } = target;

    const changeBorder = (percentage) => {
        if (percentage === "100") {
            return "green--border"
        } else if (percentage === "64") {
            return "yellow--border"
        } else if (percentage === "34") {
            return "red--border"
        }
    }

    return (
        <div className={`target--card ${changeBorder(percentage_complete)}`}>
            <div className="card--header">
                <h2 className="card--heading">{title}</h2>
                <p className="location">
                    <img src={location_icon} alt="location icon" className="location--icon" />
                    <span className="location--name">{location}</span>
                </p>
            </div>
            <div className="target--amounts">
                <div className="target">
                    <h4 className="target--heading">Month Target</h4>
                    <div className="amount">{month_target}</div>
                </div>
                <div className="target">
                    <h4 className="target--heading">Achieved</h4>
                    <div className="amount">{achived}</div>
                </div>
                <div className="target">
                    <h4 className="target--heading">Left to go</h4>
                    <div className="amount">{left_to_go}</div>
                </div>
                <div className="target">
                    <h4 className="target--heading">Daily Target</h4>
                    <div className="amount">{daily_target}</div>
                </div>
                <ProgressBar />
            </div>
        </div>
    )
}

export default TargetCard;