import React from "react";
import "./Sidebar.css";

import company_logo from "../../assets/media/logo.png";
import dart_icon from "../../assets/media/dart.svg";

const Sidebar = () => {
    return (
        <div className="sidebar">
            <div className="company--profile">
                <img src={company_logo} alt="" className="company--logo" />
                <div className="company--profile-info">
                    <h4 className="company--name">Ugowest Services Limited</h4>
                    <p className="tagline">Company Tagline</p>
                </div>
            </div>
            <div className="sidebar--items">
                <ul className="sidebar--links">
                    <li className="sidebar--link empty"></li>
                    <li className="sidebar--link empty"></li>
                    <li className="sidebar--link empty"></li>
                    <li className="sidebar--link empty"></li>
                    <li className="sidebar--link empty"></li>
                    <li className="sidebar--link empty"></li>
                    <li className="sidebar--link empty"></li>
                    <li className="sidebar--link empty"></li>
                    <li className="sidebar--link active">
                        <img src={dart_icon} alt="dart icon" className="sidebar--link-icon" />
                        <span className="title">Target</span>
                    </li>
                </ul>
            </div>
        </div>
    )
}

export default Sidebar;